import { Container, Button,FormGroup, Row, Form, Col } from 'react-bootstrap';
import React from 'react';

import { useState, useEffect } from 'react';
import queryString from 'query-string';
const url="https://broccoli.westudents.it/v4/ads/events/app";
var Parse = require('parse/node');

Parse.initialize(
  '6MbjfPZ82pdNioK4VGVpLGVZO9U2gwPg1wcJiMer',
  'ik1KaVsbPS4V9yxAq90vFagE0ogbMahjnstrcY5c'
); //PASTE HERE YOUR Back4App APPLICATION ID AND YOUR JavaScript KEY
Parse.serverURL = 'https://parseapi.back4app.com/';

function FormWe(props) {
  const [firstName, setFirstname] = useState('');
  const [lastName, setLastname] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [ws_campaign_id, setCampaign] = useState('');
  const [ws_placement_id, setPlacement] = useState('');
  const [bearer, setBearer] = useState('');
  const [ready,setReady] = useState(false);

  useEffect(() => {
    const createData = async () => {
      const values = queryString.parse(window.location.search); //it takes the url and it uses the method search to obtain an object with all parameters
      setFirstname(values.firstname);
      setLastname(values.lastname);
      setEmail(values.email);
      setPhone(values.phone);
      setCampaign(values.ws_campaign_id);
      setPlacement(values.ws_placement_id);
      setBearer(values.bearer);
      setReady(true);}
    createData();
  }, []);

  useEffect(()=>{
    const postWeS = async ()=>{
      const requestOptions = {
        method: 'POST',
        headers: {
          'Authorization': 'Bearer ' +  bearer ,
          'Content-Type': 'application/json',
        },
        body: {
          category: "ADS",
          action: "VIEW",
          campaign: ws_campaign_id,
          placement: ws_placement_id ,
        },
      };
      console.log(requestOptions)
        const response = await fetch(url, requestOptions)
        if (response.ok) {
          const responseBody = await response.json();
          console.log(responseBody);
        } else {
          try {
            const err = await response.json();
            throw err.message;
          } catch (err) {
            throw err;
          }
        }
    }
    if(ready)
      postWeS();
  },[ready])
  
  async function saveNewPlayer() {
    //Create your Parse Object
    let dispensoCustomer = new Parse.Object('DispensoWS');

    //Define its attributes
    dispensoCustomer.set('Name', firstName);
    dispensoCustomer.set('Surname', lastName);
    dispensoCustomer.set('emailContact', email);
    dispensoCustomer.set('telefono', phone);
    try {
      //Save the Object
      await dispensoCustomer.save();
    } catch (error) {
      console.log(error);
    }
  }
  const handleNameForm = (ev) => {
    setFirstname(ev.target.value);
  };
  const handleEmailForm = (ev) => {
    setEmail(ev.target.value);
  };
  const handleSurnameForm = (ev) => {
    setLastname(ev.target.value);
  };

  return (
    <>
      <Container fluid>
        <Row>
          <Col>
            <Form.Label className="Mont">Nome*</Form.Label>
            <Form.Control
              required
              type="text"
              value={firstName}
              onChange={handleNameForm}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Form.Label className="Mont">Cognome*</Form.Label>
            <Form.Control
              required
              type="text"
              value={lastName}
              onChange={handleSurnameForm}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Form.Label className="Mont">Email*</Form.Label>
            <Form.Control
              required
              type="email"
              value={email}
              onChange={handleEmailForm}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Form.Label className="Mont">Telefono*</Form.Label>
            <Form.Control required value={phone} onChange={handleNameForm} />
          </Col>
        </Row>
        <Row>
          <Col>
            <Form.Check
            className="Mont"
              required
              type="checkbox"
              label="Vorrei ricevere email da Dispenso su consigli, novitÃ  e prove gratuite"
            />
          </Col>{' '}
        </Row>
        <Row>
          <Col>
            <Button className="orange-button" size="lg"
              onClick={async () => {
                
                await saveNewPlayer();
                if(ws_campaign_id !== undefined && ws_placement_id!==undefined && bearer!==undefined){
                const requestOptions2 = {
                  method: 'POST',
                  headers: {
                    'Authorization': 'Bearer ' + bearer ,
                    'Content-Type': 'application/json',
                  },
                  body: {
                    category: "ADS",
                    action: "CONVERSION",
                    campaign: ws_campaign_id ,
                    placement:  ws_placement_id ,
                  },
                };
                const res = await fetch(url, requestOptions2)
              }}}
            >
              CONTATTAMI
            </Button>
          </Col>{' '}
        </Row>
      </Container>
    </>
  );
}
export default FormWe;
